<template>
  <div class="sales-board">
      <div class="sales-board-intro">
        <h2></h2>
        <p></p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-counter></v-counter>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-selection :selections="buyTypes" ></v-selection>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-chooser :selections="periodList"></v-chooser>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-mul-chooser :selections="versionList"></v-mul-chooser> 
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  {{ price }} 
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button" >
                    
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h2></h2>
        <p></p>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>



  </div>
</template>

<script>
import VSelection from '../../components/selection'
import VCounter from '../../components/counter'
import VChooser from '../../components/chooser'
import VMulChooser from '../../components/multiplyChooser'

export default {
  components: {
    VSelection,
    VCounter,
    VChooser,
    VMulChooser,
  },
  data () {
    return {
      buyNum: 0,
      buyType: {},
      versions: [],
      period: {},
      price: 0,
      versionList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      periodList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      buyTypes: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      isShowPayDialog: false,
      bankId: null,
      orderId: null,
      isShowCheckOrder: false,
      isShowErrDialog: false
    }
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th{
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
}
</style>



